$text-color: gainsboro;

@media (max-width: 575px) {
  input {
    width: 60vw;
  }
  .fa-book {
    padding-right: 50px;
  }
}
.fa-times {
  font-size: 20px;
  position: relative;
  top: 1px;
  right: 25px;
  color: gray;
  &:hover {
    cursor: pointer;
  }
}
.title {
  font-size: 30px;
  display: inherit;
  margin-left: 10px;
}
.header {
  padding: 0;
  background-image: url("../media/header_background.jpg");
  background-size: contain;
  color: $text-color;
}
.header_layer {
  background: rgba(0,0,0,.6);
  backdrop-filter: blur(5px);
}
.header_content {
  margin: 0;
  height: 100%;
  align-items: center;
}
select {
  outline: 0;
  border: none;
  padding-left: 10px;
  height: 35px;
  width: 200px;
  margin-left: 10px;
  background-color: $text-color;
  &:hover {
    cursor: pointer;
  }
  &:focus {
    background-color: white;
  }
}
.fa-search {
  position: relative;
  font-size: 20px;
  color: $text-color;
  &:active{
    color: gray;
  }
}
.search_bar{
  outline: 0;
  border: none;
  min-width: 50vmin;
  height: 35px;
  background-color: $text-color;
  padding-left: 10px;
  &:focus {
    background-color: white;
  }
}
.fa-book {
  padding-left: 50px;
  font-size: 70px;
  white-space: nowrap;
  color: $text-color;
  &:active {
    color: gray;
  }
}