$background-color: #e9ecf1;
$text-color: gainsboro;

@media (max-width: 575px) {
  .book_block {
    padding: 0  !important;
    margin-top: 10px !important;
  }
  .book_image {
    margin-top: 10px;
    width: 90% !important;
  }
}
@media (max-width: 767px) {
  .up_button {
    left: 80vw;
  }
}
.loading {
  min-height: 50vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.card {
  display: flex;
  align-items: center;
  padding-top: 2vw;
  padding-left: 2vw;
  padding-right: 2vw;
}
.book_image {
  width: 100%;
  transform: scale(1);
  transition-duration: 0.3s;
}
.loadmore_button, .up_button{
  font-size: 17px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
  background-color: white;
  -webkit-box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
  -moz-box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
  box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
  &:hover {
    cursor: pointer;
    background-color: $text-color;
  }
}
.loadmore_button {
  height: 50px;
  width: 150px;
}
.up_button {
  height: 50px;
  width: 50px;
  position: fixed;
  top: 80vh;
  left: 90vw;
  z-index: 9999;
}
.book_block {
  color: #000;
  text-decoration: none;
  &:hover {
    u {
      color: #000;
    }
    .book_image {
      transform: scale(1.05);
      transition-property: transform;
      transition-duration: 0.3s;
    }
  }
}
.fa-redo-alt {
  padding: 0;
  font-size: 20px;
  height: 20px;
  width: 20px;
  animation: Spinner 1s linear infinite;
}
@keyframes Spinner {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

